  x = x.map(function (x) {
    return x * 1000;
  });

  // get deployment timeStart and timeEnd
  var deploymenttStart = moment_default.a.unix(parseFloat(x[0] / 1000)).format('L');
  var deploymenttEnd = moment_default.a.unix(parseFloat(x[x.length - 1] / 1000)).format('L');

  var plot = new plt.figure({
    title: "Deployment: " + deploymentName + " Location: " + locationID + " Start deployment: " + deploymenttStart + " End deployment: " + deploymenttEnd,
    tools: tools,
    width: 800,
    height: 350,
    x_axis_type: 'datetime',
    x_axis_label: 'Time',
    y_axis_label: 'Number of particles [-]',
    background_fill_color: "#F2F2F7"
  });

